@import "variables";
/* Slider */
main .slick-slider ul li:before { content: none; }
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
    clear:both; 
    ul, li {
      list-style-type:none;
      li { 
        padding-left: 0; margin:0;
        text-indent: 0;
      }
      li:before {
        display:none;
      }
    }
}

.slick-slider *{
  min-height: 0;
  min-width: 0;
  // background: rgba(255, 0, 0, 0.24)
  }
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    clear:both;
    // width: 100%; 
    height: auto;
    margin-left: auto;
    margin-right: auto;
    min-width: 100%;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;     
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
        // width: 100%;
        // max-width: 1170px;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
        position: relative;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}
 

.slick-slider>div>.caption,.slick-slider>div{display:none;opacity:0}
.slick-slider>div:first-child{display:block;}
.slick-slider.slick-initialized>div, .slick-slider.content-feature>div:first-child{display:block;opacity:1; }
 
// Correct for quote slide
.slick-slider .slick-dots li {
  margin:0;
}
.slick-slider .slick-dots li button:before, .slick-slider .slick-dots li.slick-active button:before {color: rgba(17, 17, 17, .85);}
.slick-slider .slick-dots li button:before {
  opacity: .3;
  font-size: 0.75rem; // needs extra tlc to get to the right size
}
.slick-slider .slick-dots li.slick-active button:before  { opacity: 1; }
// end Correct for quote slide

.slick-slider.carousel-slider .slick-dots { position: relative; }
 
.simple-slickslider {
  padding:0;
  padding-bottom: 1.5rem;
  .slick-dots {
    position: absolute;
    bottom: .5rem;
  }
}  
.simple-slickslider .caption {
  color: #fff;
  text-align: center; 
  padding: .5rem;
  p{ margin:0;}
}
@media (max-width: 767px) {
  .simple-slickslider { 
    .slick-prev { left:-1.5rem;}
    .slick-next { right:-1.5rem;} 
  }  
}
@media (min-width: 768px) {
  .simple-slickslider { background: transparent; padding-bottom:0;}
  .simple-slickslider .caption {
    position: absolute;
    right: 15%;
    bottom: 2rem;
    left: 15%;
    z-index: 5; 
  
    text-shadow: 0 1px 2px rgba(0, 0, 0, .6);
    background-color: rgba(0,0,0,0.6);
    // border-radius: 15px;
    
  }
  .simple-slickslider { 
    margin: -.5rem;
    .slick-slide {
      margin: .5rem;
    }
  }

} 


// Joe's custom arrows, demo on quotes
.slick-slider {
  position: relative;
  background-position: 50% 50%;
  background-image: url("../../resources/img/icons/ajax-loader2.gif");
  background-repeat: no-repeat;

  &.slick-initialized {
    background: none;
  }

  &.slick-slider-no-arrows {
    .slick-arrow { display:none !important;}
  }
  .slick-arrow { 
    z-index: 3;
      width: 3.4375rem;
      height: 2.5rem;
      border-radius: 24px;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.05); 
      // border: solid 1px rgba(0, 0, 0, 0.5); // debugging
      border: solid 1px rgba(0, 0, 0, 0.05);
      background-color:#fff;
      &:before { display:none;}
      &:hover { background-color: $pale-gray}
    
      .button-left:before, .button-right:before {
      content: "";
      background-image: url('../../resources/img/icons/arrow@3x.png');
      background-position: center;
      margin: 0 auto; 
      background-repeat: no-repeat;
      height: 1rem;
      width: 1rem;
      display: block;
      background-size: contain;
    } 
    .button-left:before {
      background-image: url('../../resources/img/icons/arrow@3x.png');
      transform: rotate(180deg);
    }
 
  }
  .slick-dots { bottom: auto;}
}

.carousel-slider .slick-dots {  
  text-align: left;
}

.carousel-slider .slick-dots li button:before  {
  display: none;
  }
.carousel-slider .slick-dots li { display: inline-block; height: auto; width: auto;}
.carousel-slider .slick-dots li button { 
  height: auto; width: auto;  padding: 8px 12px; border-radius:0; margin-right: 5px;
  width: 10px; background-color:rgba(0, 0, 0, 0.1); 
}
.carousel-slider .slick-dots li.slick-active button  {
  background-color: $primary;
}
.carousel-slider .carousel-feature-text { display:none;}
.carousel-slider.slick-dotted.slick-slider { margin-bottom: 12px;}
@media (min-width: 992px) {
  .carousel-slider .slick-slide {
    transition: opacity .25s ease-in-out;
  }
  .carousel-slider .slick-active.slick-slide + .slick-slide {
    padding-left: 1rem;
    opacity: .5;
  }
}
@media (max-width: 767px) {
  .carousel-slider {
    .slick-arrow{
      position: absolute;
      right: 10px;
      left: auto;
      top:auto;
       bottom:15px;
       width:50px;
       
  } 
  
  .slick-prev.slick-arrow {
      margin-right:50px;
      border-bottom-right-radius:0;
      border-top-right-radius:0;
  }
  
  .slick-next.slick-arrow { 
      border-bottom-left-radius:0;
      border-top-left-radius:0;
  }
  }
}

.bg-inverted, .sectional-inverted {
  .slick-slider .slick-dots li button, .slick-slider .slick-dots li.slick-active button:before {
    color: $light;
  }
  .carousel-slider .slick-dots li button { background-color:rgba(255, 255, 255, 0.2); } 
  .carousel-slider .slick-dots li.slick-active button { background-color: $light; } 
}
.header-carousel { padding-bottom: 2rem; }
@media (min-width: $screen-md) {
  .header-carousel { 
    padding-right: 1rem; 
    padding-left: 1rem;  }
}
@media (min-width: $screen-lg) {
  .header-carousel { padding-right: 0; padding-left: 0; padding-bottom: 0; }
  .header-carousel .carousel-slider.slick-slider img {
    height: 350px;
  }
}


